import EventEmitter from 'eventemitter3';
import { MenuItem } from './items-menu/menuItem';
import { ContextItem } from './items-context/contextItem';
import { PlayerOptions } from './types';
import { processOptions } from './options';
import { addClass, createEl, getEl } from './utils';
import { CLASS_PREFIX } from './constants';
import { ContentLayer } from './layers/content';

export class Player extends EventEmitter {
  private el: HTMLElement | null;

  private readonly video: HTMLVideoElement;

  private opts: PlayerOptions;

  private readonly contentLayer: ContentLayer;

  readonly dom: HTMLElement;

  menuItems: MenuItem[] = [];

  contextItems: ContextItem[] = [];

  constructor(opts: PlayerOptions) {
    super();
    this.opts = processOptions(opts);
    this.el = getEl(opts.el);
    this.dom = createEl('div', 'rplayer', false);
    if (this.opts.video) {
      this.video = this.opts.video;
      addClass(this.video, 'video', CLASS_PREFIX);
    } else {
      this.video = createEl('video', 'video');
    }

    this.contentLayer = new ContentLayer(this);
  }

  toast(html: string): void {}

  mount(el?: PlayerOptions['el']): void {
    if (el) this.el = getEl(el);
    if (!this.el) throw new Error('require `el` option');
    this.el.appendChild(this.video);
    this.el.appendChild(this.contentLayer.dom);
    this.el.appendChild(this.dom);
  }
}
