import { CLASS_PREFIX } from '../constants';

export function addClass(dom: HTMLElement, cls = '', prefix = ''): void {
  cls = cls.trim();
  if (!cls) return;
  if (dom.classList) {
    cls.split(' ').forEach((c) => dom.classList.add(prefix + c));
  } else {
    dom.setAttribute('class', `${dom.className.trim()} ${cls}`);
  }
}

export function createEl<K extends keyof HTMLElementTagNameMap = 'div'>(
  tag?: K,
  cls?: string,
  noPrefix?: boolean,
): HTMLElementTagNameMap[K] {
  const el = document.createElement(tag || 'div');
  if (cls) addClass(el, cls, noPrefix ? '' : CLASS_PREFIX);
  return el as any;
}

export function isString(o: unknown): o is string {
  return typeof o === 'string';
}

export function getEl(el: HTMLElement | string | undefined | null): HTMLElement | null {
  if (!el) return null;
  if (isString(el)) return document.querySelector(el);
  return el;
}
